      jQuery( '#title' ).blur();
    },

    createdyntext() {
      var newid = this.maxid();
      store.domelements.push(
        {
          "id" : newid,
          "idstr": 'ogpaintitem' + this.maxid(),
          "type": "dyntext",
          "layername": "Post Title",
          "frame": { translatex : 0, translatey: 0, rotate: 0, width: 300, height: 200}, 
          "dyncaption": "title",
          "caption": "Dynamic Text",
          "metafield": "",
          "color": "#000000",
          "opacity": "100",
          "fontsize": "40",
          "fontstyle": "normal",
          "lineheight": "1.4",
          "fontweight": "400",
          "fontfamily": "Open+Sans",
          "texttransform": "none",
          "verticalalign": "start",
          "horizontalalign": "left",
          "basicouterclass":"og-text",
          "basicinnerclass":"og-text-inner",
